import {OptionsPage} from './OptionsPage';
import {__} from '../Translator';
import BuildHelper from '../BuildHelper';

export class TestOptionsPage extends OptionsPage {
    constructor() {
        super('test');
    }

    render() {
        const wrapper = BuildHelper.div('options-wrapper');

        const intBlock = this.createBlock(
            'int',
            'test_int_value',
            __('Valeur entire', 'admin'),
            null,
            {
                description: __('Un nombre entier  saisir', 'admin'),
            },
            'test'
        );

        const floatBlock = this.createBlock(
            'float',
            'test_float_value',
            __('Valeur dcimale', 'admin'),
            null,
            {
                description: __('Un nombre  virgule pour les tests', 'admin'),
            }
        );

        const stringBlock = this.createBlock(
            'string',
            'test_string_value',
            __('Texte simple', 'admin'),
            null,
            {
                description: __('Une chane de texte modifiable', 'admin'),
                placeholder: __('Entrez du texte...', 'admin'),
            },
            'test'
        );

        const boolBlock = this.createBlock(
            'bool',
            'test_bool_value',
            __('Activer la fonctionnalit ?', 'admin'),
            null,
            {
                description: __('Active ou dsactive la fonctionnalit de test', 'admin'),
            },
            'test'
        );

        const selectBlock = this.createBlock(
            'select',
            'test_select_value',
            __('Type de choix', 'admin'),
            null,
            {
                description: __('Choisissez une des options', 'admin'),
                options: [
                    {value: 'alpha', label: 'Alpha'},
                    {value: 'beta', label: 'Bta'},
                    {value: 'gamma', label: 'Gamma'},
                ]
            },
            'test'
        );

        const mediaBlock = this.createBlock(
            'media',
            'test_media_value',
            __('Fichier mdia', 'admin'),
            null,
            {
                description: __('Slectionnez un mdia pour le test', 'admin'),
                mimeTypes: ['image/jpeg', 'image/png', 'image/webp'],
                label: __('Choisir un mdia', 'admin')
            },
            'test'
        );

        // Ajout  l'interface
        [
            intBlock,
            floatBlock,
            stringBlock,
            boolBlock,
            selectBlock,
            mediaBlock,
        ].forEach(block => block.render(wrapper));

        this.container.appendChild(wrapper);
    }
}
